#RGB {
	text-align: center;

	h1 {
		margin: 0px;
		font-size: 50px;
		padding: 40px 0px;
		line-height: 0px;
		color: cadetblue;
	}

	.RGB {
		display: inline;
		font-size: 30px;
	}

	.RGB_button {
		margin: 10px 10px;
		height: 50px;
		width: 100px;
		font-size: 30px;
		background-color: var(--button-color);
		border: 1px solid var(--border-color);
		border-radius: 10px;
	}
}